import React from 'react'
import "./css/Post.css";
import { Avatar } from '@material-ui/core';
import { ArrowDownwardOutlined, ArrowUpwardOutlined, ChatBubbleOutline, MoreHorizOutlined, RepeatOneOutlined, ShareOutlined } from '@material-ui/icons';
import { useState } from 'react';
import CloseIcon from '@material-ui/icons/Close';
import 'react-responsive-modal/styles.css';
import { Modal } from "react-responsive-modal";
import ReactQuill from "react-quill";
import 'react-quill/dist/quill.snow.css';

function Post() {
    const [isModalOpen, setIsModalOpen]=useState(false);
    const Close = <CloseIcon />;
  return (
    <div className="post">
       <div className="post__info">
            <Avatar />
            <h4> User Name</h4>
            <small>Timestamp</small>
       </div>
       <div className="post__body">
            <div className="post__question">
                <p> This is Question</p>
                <button onClick={()=>setIsModalOpen(true)} className="post__btnAnswer">Answer</button>
                <Modal
                    open={isModalOpen} 
                    closeIcon={Close} 
                    onClose={()=>setIsModalOpen(false)} 
                    closeOnEsc
                    center
                    closeOnOverlayClick={false}
                    styles={{
                        overlay: {
                        height: "auto",
                        },
                    }}
                    >
                    <div className="modal__question">
                        <h1>This is Question</h1>
                        <p>Asked By {" "}<span className='name'>username</span> on <span className='name'>timeframe</span></p>
                    </div>
                    <div className='modal__answer'>
                        <ReactQuill placeholder='Enter your answer'/>
                    </div>
                    <div className='modal__buttons'>
                        <button className='cancle' onClick={() => setIsModalOpen(false)}>
                            Cancel
                        </button>
                        <button type="submit" className="add">
                            Submit Answer
                        </button>
                    </div>
                </Modal>
            </div>
       </div>
       <div className="post__footer">
            <div className="post__footerAction">
                <ArrowUpwardOutlined />
                <ArrowDownwardOutlined />
            </div>
            <RepeatOneOutlined />
            <ChatBubbleOutline />
            <div className="post__footerLeft">
                <ShareOutlined />
                <MoreHorizOutlined />
            </div>
       </div>
       <p> 1 Answer</p>
       <div  style = {{
        margin: "50px 0px 0px 0px",
        padding: "5px 0px 0px 20px",
        borderTop:"ipx solid lightgray",
       }} 
       classname="post__answer">
            <div 
            style= {{
                display:"flex",
                flexDirection:"column",
                width:"100%",
                padding:"10px 5px",
                borderTop:"1px solid lightgray",
            }} 
            className="post-answer-container">
                <div 
                style= {{
                    display:"flex",
                    alignItems:"center",
                    marginBottom:"10px",
                    fontSize:"12px",
                    fontWeight:600,
                    color:"#888",
                }}
                className="post-answered">
                    <Avatar />
                    <div 
                    style={{
                        margin:"0px 10px",

                    }}
                    className="post-info">
                        <p>Username</p>
                        <span> Timestamp </span>
                    </div>
                </div>
                <div className="post-answer"> This is test answer</div>
            </div>
       </div>
    </div>
  )
}

export default Post